body {
    background-color: #282831;
    box-sizing: border-box;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.header {
    background-color: #282831;
    color: #9e9ea3;
    font-size: 3em;
    padding: 1rem;
    text-align: center;
    text-transform: uppercase;
}

input:focus {
    outline: none;
}

.search-form {
    text-align: center;
    margin-top: 1rem;
    margin-bottom: 3rem;
}

.search {
    color: #9e9ea3;
    padding: 0.5rem 1rem;
    font-size: 1.25rem;
    line-height: 1.5;
    border-radius: 0.8rem;
    border: 1px solid #282831;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    background-color: #2f2f38;
}

img {
    border-radius: 50%;
    height: 60px;
    width: 60px;
}

.table-users {
    border: 1px solid #282831;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    max-width: calc(100% - 2em);
    margin: 1em auto;
    overflow: hidden;
    width: 90%;
}

table {
    width: 100%;
}

.fa-sort {
    margin-left: 10px;
    visibility: hidden;
    color: #9e9ea3;
}

.table-users table th:hover .fa-sort {
    cursor: pointer;
    visibility: visible;
}

table td,
table th {
    color: #9e9ea3;
    padding: 10px;
}
table td {
    text-align: center;
    vertical-align: middle;
}

table th {
    background-color: #2b2a35;
    font-weight: 300;
}
table tr:nth-child(2n) {
    background-color: #2b2a35;
}
table tr:nth-child(2n + 1) {
    background-color: #2f2f38;
}

@media screen and (max-width: 700px) {
    table,
    tr,
    td {
        display: block;
    }

    td:first-child {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 100px;
    }
    td:not(:first-child) {
        clear: both;
        margin-left: 100px;
        padding: 4px 20px 4px 90px;
        position: relative;
        text-align: left;
    }
    td:not(:first-child):before {
        color: #91ced4;
        content: "";
        display: block;
        left: 0;
        position: absolute;
    }
    td:nth-child(2):before {
        content: "Name:";
    }
    td:nth-child(3):before {
        content: "Email:";
    }
    td:nth-child(4):before {
        content: "Phone:";
    }
    td:nth-child(5):before {
        content: "Comments:";
    }

    tr {
        padding: 10px 0;
        position: relative;
    }
    tr:first-child {
        display: none;
    }
}
@media screen and (max-width: 500px) {
    .header {
        background-color: #282831;
        color: #9e9ea3;
        font-size: 2em;
        font-weight: 700;
        padding: 0;
        text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.1);
    }

    img {
        border: 3px solid;
        border-color: #282831;
        height: 100px;
        margin: 0.5rem 0;
        width: 100px;
    }

    td:first-child {
        background-color: #2b2a35;
        border-bottom: 1px solid #2b2a35;
        border-radius: 10px 10px 0 0;
        position: relative;
        top: 0;
        transform: translateY(0);
        width: 100%;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    }
    td:not(:first-child) {
        margin: 0;
        padding: 5px 1em;
        width: 100%;
    }
    td:not(:first-child):before {
        font-size: 0.8em;
        padding-top: 0.3em;
        position: relative;
    }
    td:last-child {
        padding-bottom: 1rem !important;
    }

    tr {
        background-color: #2f2f38 !important;
        border: 1px solid #2f2f38;
        border-radius: 10px;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
        margin: 0.5rem 0;
        padding: 0;
        width: 100%;
    }

    .table-users {
        border: none;
        box-shadow: none;
        overflow: visible;
    }
}
